@media screen and (max-width:600px){
    .home{
        display: grid;
        grid-template-columns: 1fr;
        
        
    }
    .home-con{
        margin: 2px;
        background-color:rgba(165, 166, 166, 0.401);
        height: 300px;
        border: 1px rgb(31, 3, 117) solid;
    }
    .home-con h2{
        background-color: aquamarine;
    }
}
@media screen and (min-width:600px) {
    .home{
        margin-top: 50px;
        display: grid;
        grid-template-columns: repeat(3,1fr);

    }
    .home-con{
        margin: 2px;
        background-color:rgba(165, 166, 166, 0.401);
        height: 500px;
        border: 1px rgb(31, 3, 117) solid;
    }
}