@media screen and (min-width:600px) {
    .menu ul{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }
    .menu ul li {
        list-style: none;
        border: 1px solid gray;
        padding: 5px;
        margin: 10px;
        width: 150px;
        text-align: center;
        box-shadow: 0px 0px 2px gray;
    }
    .menu a{
        text-decoration: none;
        font-size: 1.2rem;
    }
}
@media screen and (max-width:600px){
    .menu{
        list-style: none;
        outline: none;
    }
    .menu ul{
        display: grid;
        grid-template-columns: 1fr 1fr;
        text-align: center;
    }
    .menu ul li{
        text-decoration: none;
        padding: 10px;
        list-style: none;
        border: 1px solid gray;
        margin: 5px;
    }
    .menu a{
        text-decoration: none;
    }
}