*{
    margin: 0;
    padding: 0;
}
.header{
    margin: 0;
    padding: 0;
    background-image: linear-gradient(to right, #0061ff , #60efff);
    font-size: 15px;
    display: flex;
    justify-content: space-between;

}
@media screen and (max-width:600px) {
    #nav-list ul{
        display: grid;
        grid-template-columns: 1fr 1fr; 
        justify-content: center;
        margin: 2px;
    }
    #nav-list ul li{
        display: flex;
        justify-content: center;
        padding: 1rem;
        border: 1px solid gray;
    }
    
}